import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function HeaderStyle(){
    darkLightMode("header");
    setStyle();
    document.getElementsByTagName('body')[0].onresize = function(){setStyle()};
}

function setStyle(){
    eId("header").className = eId("header").className.replace(" card-title", "") + " card-title";
}

export default HeaderStyle;